.section-signin {
  font-family: "Open Sans", sans-serif;
  line-height: 1.6;
  background-color: var(--color-primary);
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.signin {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  padding-left: 2rem;
  padding-right: 2rem;

  @media only screen and (max-width: $bp-smallest) {
    width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
  }

  &__heading {
    font-size: 2.4rem;
    font-weight: 400;
    color: white;
    margin-bottom: 2.75rem;
  }

  &__navigation {
    list-style: none;
    margin-bottom: 2.75rem;
  }
  &__item {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 1.5rem;
    }
  }
  &__link {
    &:link,
    &:visited {
      font-size: 1.8rem;
      text-decoration: none;
      color: var(--color-grey-light-4);
      text-decoration: none;
      display: inline-block;
      transition: all 0.2s;
    }
    &:hover,
    &:active {
      color: var(--color-secondary);
      border-bottom: 1px solid var(--color-secondary);
    }
    &--active {
      color: var(--color-secondary) !important;
      border-bottom: 1px solid var(--color-secondary);
    }
  }

  &__box {
    background-color: white;
    box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
    padding: 4rem;
    width: 80%;
    border-radius: 3px;
  }
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  label {
    display: inline-block;
    font-size: 1.5rem;
    line-height: 1.5;
    font-weight: 600;
    text-align: left;
    color: var(--color-grey-dark-1);
    margin-bottom: 0.5rem;
  }
  button {
    padding: 1.5rem 3rem;
    font-size: 1.5rem;
    background: var(--color-primary);
    color: white;
    border: none;
    border-radius: 4px;

    &:hover {
      cursor: pointer;
    }
    &:focus,
    &:active {
      outline: none;
    }
    &:disabled {
      opacity: 0.65;
      cursor: not-allowed;
    }
  }
}
._form_group {
  margin-bottom: 1rem;
}
._form_control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1.5rem;
  line-height: 1.5;
  color: var(--color-primary);
  background-clip: padding-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  &:focus {
    outline: none;
  }
  &::-ms-expand {
    background-color: transparent;
    border: 0;
  }
  &::placeholder {
    color: #6c757d;
    /* Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526.*/
    opacity: 1;
  }
}
._form_text {
  display: block;
  margin-top: $form-text-margin-top;
}
.red {
  font-size: 1.5rem;
  background-color: #ffe8e6;
  color: #db2828;
  padding: 0.375rem 0.75rem;
  box-shadow: 0 0 0 1px #db2828 inset, 0 0 0 0 transparen;
}
._error {
  input {
    background-color: #fff6f6;
    border-color: #e0b4b4;
    color: #9f3a38;
    box-shadow: none;
  }
  label {
    color: #9f3a38;
  }
}
.forgot-pasword {
  &:link,
  &:visited {
    text-decoration: none;
    color: white;
    display: inline-block;
    padding: 0.75rem 1.25rem;
    margin-top: 2rem;
    font-size: 1.4rem;
  }
}
