.container {
  width: 100%;
}

.header {
  font-size: 1.4rem;
  height: 6.4rem;
  background-color: var(--color-primary);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  outline: none !important;

  @media only screen and (max-width: $bp-smallest) {
    flex-wrap: wrap;
    align-content: space-around;
    height: 11rem;
  }
}

.logo {
  height: 3.25rem;
  width: 3.25rem;
  margin-left: 3rem;
  fill: var(--color-secondary);
}

////////////////////////////////
///// User-nav
.left-nav {
  display: flex;
  align-items: center;
  align-self: stretch;
  & > * {
    padding: 0 2rem;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
  }
  &_link {
    text-decoration: none;
    color: #f2f4f7;
  }
  & > *:hover {
    //background-color: var(--color-grey-light-2);
  }

  &__icon-box {
    position: relative;
    & > *:hover {
      color: var(--color-secondary);
    }
  }
}
// USER NAVIGATION
.user-nav {
  align-self: stretch;
  display: flex;
  align-items: center;
  &_link {
    text-decoration: none;
    color: #f2f4f7;
  }
  & > * {
    padding: 0 2rem;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
  }

  & > *:hover {
    //background-color: var(--color-grey-light-2);
  }

  &__icon-box {
    position: relative;
    & > *:hover {
      color: var(--color-secondary);
    }
  }

  &__icon {
    height: 2.25rem;
    width: 2.25rem;
    fill: white;
    & > *:hover {
      fill: var(--color-secondary);
    }
  }

  &__notifications {
    font-size: 0.8rem;
    height: 1.75rem;
    width: 1.75rem;
    border-radius: 50%;
    background-color: var(--color-red);
    color: white;
    position: absolute;
    top: 1.5rem;
    right: 1.1rem;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__user-photo {
    height: 3.75rem;
    width: 3.75rem;
    border-radius: 50%;
    margin-right: 1rem;
  }

  &__user-name {
    color: white;
    & > *:hover {
      color: var(--color-secondary);
    }
  }
}
