:root {
  --color-primary: rgb(18, 22, 28);
  --color-primary-light: #577aab;
  --color-primary-dark: #36378a;

  --color-secondary: rgb(240, 185, 11);

  --color-grey-light-1: rgb(132, 142, 156);
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #ccc;

  --color-grey-dark-1: #414656;
  --color-grey-dark-2: #39394c;
  --color-grey-dark-3: #333041;

  --color-red: #d8354d;

  --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  --shadow-light: 0 2rem 5rem rgba(0, 0, 0, 0.06);

  --line: 1px solid var(--color-grey-light-2);
}
$bp-largest: 75em; // 1200px
$bp-large: 68.75em; // 1100px
$bp-medium: 56.25em; // 900px
$bp-small: 37.5em; // 600px
$bp-smallest: 31.25em; // 500px

$form-text-margin-top: 0.25rem;
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 75%; // 1rem = 10px, 10px/16px = 62.5%
  @media only screen and (max-width: $bp-large) {
    font-size: 62.5%; //50%
  }
}
body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 1.6;
}
